import React, { Component } from 'react'

import Ball from './Ball'

import codeTypes from '../../jelly/codeTypes'

export default class BallsBox extends Component {

  constructor(props) {
    super(props)
    this.atoms0 = null
    this.quicks0 = null
    this.comp0 = null
  }

  render0() {
    const props = this.props
    const click = props.click
    const type = props.type
    const atoms = props.atoms
    const quicks = props.quicks

    const codeType = codeTypes[type]
    const symbols = codeType.symbols
    const style0 = {
      border: "1px solid",
      borderColor: "black"
    }
    const style1 = {
      border: "4px solid",
      borderColor: codeType.color
    }
    const style2 = {
      border: "2px solid",
      borderColor: codeType.color,
      background: codeType.color
    }
    var i = 1

    return (
      <div style={style0}>
        <div style={style1}>
          <div style={style2} title={codeType.desc}>{codeType.name}</div>
          {symbols.filter(c=>codeType.atom?atoms.get(c):quicks.get(c)).map(c =>
            <Ball type={type} key={i++} click={click}>{c}</Ball>
          )}
        </div>
      </div>
    )
  }

  render() {
    const props = this.props
    const atoms = props.atoms
    const quicks = props.quicks

    if(!this.comp0 || this.atoms0 !== atoms || this.quicks0 !== quicks) {
      this.atoms0 = atoms
      this.quicks0 = quicks
      this.comp0 = this.render0()
    }

    return this.comp0
  }
}
