import React from 'react'

import Ball from './Ball'
import { parseChain, parseLiter } from '../../jelly/parse'

import atomsDesc from '../../jelly/atomsDesc'
import quicks from '../../jelly/quicks'
import codeDesc from '../../jelly/codeDesc'
import splice from '../../utils/splice'

import '../Table/Table.css'

const style = "minimalistBlack"

const BallRow = (props) => {
  let code = props.code
  let title
  if(props.codeType.atom) {
    title = atomsDesc[code]
  } else {
    const quick = quicks[code]
    if(quick) {
      title = quick.title
    } else if(props.literal) {
      title = "Literal: number (complex/float/int), string, or list."
    } else {
      title = "?"
    }
  }

  if(props.literal && code.length > 20) {
    code = splice(code, 12, code.length-18, "...")
  }

  return <tr>
    <td>
      <center>
        <Ball click={props.click} literal={props.literal} full={true}>{code}</Ball>
      </center>
    </td>
    <td>{title}</td>
  </tr>
}

const parseLink = (click, link) => {
  var i = 1
  const chainList = parseChain(link)
  return chainList.map(match=>{
    const c = match
    const codeType = codeDesc(c)
    if(codeType.type === "undef") {
      var litlist = parseLiter(c)
      return litlist.map(lit => {
        return <BallRow key={i++} codeType={codeType} click={click} literal={true} code={c}/>
      })
    } else {
      return <BallRow key={i++} codeType={codeType} click={click} literal={false} code={c}/>
    }
  })
}

const BallsReport = (props) => {
  const chain = props.chain
  const click=props.click

  return (<div>{
   !chain?"":
    <table className={style}>
    <thead>
      <tr>
        <th><center>Ball</center></th>
        <th><center>Description</center></th>
      </tr>
    </thead> 
    <tbody>
    {
      chain.split(/[\n¶]/).map(link=>parseLink(click, link))
    }
    </tbody>
    </table>
  }</div>)
}

export default BallsReport
