import type { Schema } from 'duxen'
import { fromJS } from 'immutable-sorted'

import atoms from '../../jelly/atoms'
import atomsDesc from '../../jelly/atomsDesc'
import quicks from '../../jelly/quicks'

const atomsDescLc = {}
Object.keys(atomsDesc).forEach(key => {
  var call = atoms[key].call.toLowerCase()
  if(call.startsWith("lambda ")) {
    call = call.substring(7)
  } else if(call.startsWith("lambda: ")) {
    call = call.substring(8)
  }
  atomsDescLc[key] = atomsDesc[key].toLowerCase() + " " + call
})

const quicksDescLc = {}
Object.keys(quicks).forEach(key=>quicksDescLc[key] = quicks[key].title.toLowerCase())

const schema:Schema = {
  link: {
    type: 'value',
    persistent: true,
    initValue: "2+2=4",
  },

  selectionStart: {
    type: 'value',
    persistent: false,
    initValue: -1,
  },

  selectionEnd: {
    type: 'value',
    persistent: false,
    initValue: -1,
  },

  filter: {
    type: 'value',
    persistent: true,
    initValue: "",
  },

  atoms: {
    type: 'value',
    persistent: false,
    initValue: fromJS(atoms),
  },

  quicks: {
    type: 'value',
    persistent: false,
    initValue: fromJS(quicks),
  },

  atomsView: {
    type:'view',
    sourceName: 'atoms',
    props: ["filter"],
    recipe: (seq, props) => {
      const f = props.filter.toLowerCase()
      return seq.filter((value, key) => {
        const s = atomsDescLc[key].toLowerCase()
        return !s || s.indexOf(f) >= 0
      }).toMap()
    },
  },

  quicksView: {
    type:'view',
    sourceName: 'quicks',
    props: ["filter"],
    recipe: (seq, props) => {
      const f = props.filter.toLowerCase()
      return seq.filter((value, key) => {
        const s = quicksDescLc[key]
        return !s || s.indexOf(f) >= 0
      }).toMap()
    },
  },
}

export default schema
