import type { Schema } from 'duxen'

const schema:Schema = {

  commands: {
    type: 'collection',
    persistent: false,
  },

  commandsView: {
    type:'view',
    sourceName: 'commands',
    props: [],
    recipe: (seq, props)=>seq.toSortedMap((a, b) => (Number(b)-Number(a))).take(100)
  },

  command: {
    type: 'value',
    initValue: ""
  },

}

export default schema
