import React from 'react'

import Ball from './Ball'
import { parseChain, parseLiter } from '../../jelly/parse'
import codeDesc from '../../jelly/codeDesc'

const parseLink = (click, link) => {
  var i = 1
  const chainList = parseChain(link)
  return chainList.map(match=>{
    const c = match
    const codeType = codeDesc(c)
    if(codeType.type === "undef") {
      var litlist = parseLiter(c)
      return litlist.map(lit=>(<Ball key={i++} click={click} literal={true} full={true}>{lit}</Ball>))
    } else {
      return <Ball key={i++} click={click} full={true}>{c}</Ball>
    }
  })
}

const Chain = (props) => {
  const chain = props.chain
  const click=props.click
  var i=1

  return (<div>{
   !chain?"Start balling...":
    chain.split(/[\n¶]/).map(link=>(
      <div key={i++}>
        {parseLink(click, link)}
      </div>
    ))
  }</div>)
}

export default Chain
