import React, {Component} from 'react'

import { Tab, Tabs, TabList, TabPanel } from 'react-tabs'

import HomeContainer from '../Home/HomeContainer'
import HomePage from '../Home/HomePage'
import BallsTablePage from '../BallsTable/BallsTablePage'
import BallsReportPage from '../BallsReport/BallsReportPage'
import RecipesPage from '../Recipes/RecipesPage'
import CreditsPage from '../Credits/CreditsPage'

// import TimeMachineContainer from '../TimeMachine/TimeMachineContainer'
// import TimeMachineControlsContainer from '../TimeMachineControls/TimeMachineControlsContainer';

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import 'react-tabs/style/react-tabs.css'

import './Menu.css'

const HomeTab = HomeContainer(HomePage)
const BallsTableTab = HomeContainer(BallsTablePage)
const BallsReportTab = HomeContainer(BallsReportPage)

export default class Menu extends Component {
  constructor(props) {
    super(props)
    this.state = {
      selectedTabIndex: this.props.state.get("selectedTabIndex"),
    }
  }

  updateTabIndex(index) {
    this.setState({selectedTabIndex: index})
    this.props.updateTabIndex(index)
  }

  render() {
  return <Tabs 
    selectedTabClassName="react-tabs__tab--selected Menu-selected"
    selectedIndex={this.state.selectedTabIndex}
    onSelect={this.updateTabIndex.bind(this)}
    >
    <TabList className="Menu">
      <Tab title="Palette"><FontAwesomeIcon icon="palette"/></Tab>
      <Tab title="Table"><FontAwesomeIcon icon="table"/></Tab>
      <Tab title="Report"><FontAwesomeIcon icon="list-ul"/></Tab>
      <Tab title="Recipes"><FontAwesomeIcon icon="book-medical"/></Tab>
      {/* <Tab title="State Inspector"><FontAwesomeIcon icon="info"/></Tab> */}
      <Tab title="Credits"><FontAwesomeIcon icon="trophy"/></Tab>
    </TabList>
    <hr/>
    <TabPanel>
      <HomeTab/>
    </TabPanel>
    <TabPanel>
      <BallsTableTab/>
    </TabPanel>
    <TabPanel>
      <BallsReportTab/>
    </TabPanel>
    <TabPanel>
      <RecipesPage/>
    </TabPanel>
    {/* <TabPanel>
      <TimeMachineControlsContainer engine={this.props.engine}/>
      <TimeMachineContainer engine={this.props.engine}/>
    </TabPanel> */}
    <TabPanel>
      <CreditsPage/>
    </TabPanel>
  </Tabs>
  }
}
