import React from 'react'

import codeTypes from '../../jelly/codeTypes'
import Ball from './Ball'

import '../Table/Table.css'

const style = "minimalistBlack"

const mapTypes = () => {
  var i = 1
  return Object.keys(codeTypes).map(type => {
    const codeType = codeTypes[type]
    const nbsp = String.fromCharCode(160)
    if(codeType.show) {
      return (
        <td key={i++}>
            <Ball key={i++} legend={true}>{codeType.sample}</Ball>
            {nbsp}
            {codeType.type}
        </td>
      )
    }
    return null
  })
}

const BallLegend = (props) => {
  return (
<table className={style}>
<tbody>
<tr>
  {mapTypes()}
</tr>
</tbody>
</table>
)}

export default BallLegend
