import React from 'react'

import MenuTabs from '../Menu/MenuTabs'
import { jbPermaLink } from '../../utils/permaLink'

const Jb = (props) => (
  <a 
    href={jbPermaLink(props.chain, props.tab?props.tab:MenuTabs[props.selectedTabIndex])} 
    target="_blank" 
    rel="noopener noreferrer"
  >
      {props.short?"JBI":"Jelly Ball It"}
  </a>
)

export default Jb
