import { connect } from 'react-redux'

import { engine, getState } from '../../engine'

import splice from '../../utils/splice'

const e = engine.subEngine('home')
const af = e.actionFactory();

const mapStateToProps = (state, repoHead, ownProps) => ({
  link: state.get("link"),
  selectionStart: state.get("selectionStart"),
  selectionEnd: state.get("selectionEnd"),
  filter: state.get("filter"),
  atoms: state.get("atomsView"),
  quicks: state.get("quicksView"),
  selectedTabIndex: repoHead.get("selectedTabIndex"),
})

const mapRepoToProps = (repo, ownProps) => mapStateToProps(e.get(e.head(repo)), e.head(repo), ownProps)

const mapDispatchToProps = (dispatch, ownProps) => ({
  setCode: (code) => dispatch(af.value("link", code)),

  setFilter: (filter) => dispatch(af.value("filter", filter)),
  
  setSelection: (start, end) => {
    dispatch(af.value("selectionStart", start))
    dispatch(af.value("selectionEnd", end))
  },

  addCode: (code) => {
    const origCode = e.get(getState(), "link")
    let selectionStart = e.get(getState(), "selectionStart")
    let selectionEnd = e.get(getState(), "selectionEnd")
    if(selectionStart < 0) {
      selectionStart = origCode.length
    }
    if(selectionEnd < 0) {
      selectionEnd = selectionStart
    }
    const newCode = splice(origCode, selectionStart, selectionEnd - selectionStart, code)
    dispatch(af.value("link", newCode))
    dispatch(af.value("selectionStart", selectionStart+code.length))
    dispatch(af.value("selectionEnd", selectionStart+code.length))
  },
})

const HomeContainer = connect(
  mapRepoToProps,
  mapDispatchToProps
)

export default HomeContainer
