import React from 'react'

import Chain from './Chain'
import InputCode from './InputCode'
import Tio from './Tio'
import Jb from './Jb'

const InputLayout = (props) => {
  const chain = props.link

  return <div>
    <h3><Chain chain={chain}/></h3>
    <h3><InputCode 
      chain={chain}
      selectionStart={props.selectionStart}
      selectionEnd={props.selectionEnd}
      change={props.setCode}
      setSelection={props.setSelection}
    /></h3>
    <h3><Jb chain={chain} selectedTabIndex={props.selectedTabIndex}/>&nbsp;&nbsp;&bull;&nbsp;&nbsp;{chain.length}&nbsp;bytes&nbsp;&nbsp;&bull;&nbsp;&nbsp;<Tio chain={chain}/></h3>
  </div>
}

export default InputLayout
