import React from 'react'

import Chain from '../components/Chain'
import Tio from '../components/Tio'
import Jb from '../components/Jb'

import recipes from '../../jelly/recipes'

import '../Table/Table.css'

const style = "minimalistBlack"

const RecipesPage = (props) => Object.keys(recipes).map(cat=>
  <div>
    <h2>{cat}</h2>
    <table className={style}>
    <thead>
      <tr>
        <th><center>Code</center></th>
        <th><center>Description</center></th>
        <th style={{textAlign: "right"}}>Len</th>
        <th><center>Links</center></th>
      </tr>
    </thead> 
    <tbody>
    {recipes[cat].map(r=><tr>
      <td><Chain chain={r.code}/></td>
      <td>{r.desc}</td>
      <td style={{textAlign: "right"}}>{r.code.length}</td>
      <td>
        <center>
        <Jb short={true} chain={r.code} selectedTabIndex={2}/>
        &nbsp;
        <Tio short={true} chain={r.code} args={r.args}/>
        </center>
      </td>
    </tr>)
    }
    </tbody>
    </table>
  </div>
)

export default RecipesPage
