import React, { Component } from 'react'

import atoms from '../../jelly/atoms'
import atomsDesc from '../../jelly/atomsDesc'
import quicks from '../../jelly/quicks'
import codeDesc from '../../jelly/codeDesc'
import codeTypes from '../../jelly/codeTypes'

import css from './Ball.module.css'

export default class Ball extends Component {

  constructor(props) {
    super(props)
    this.code0 = ""
    this.comp0 = null
  }

  click(e) {
    const name = e.target.getAttribute("name")
    if(this.props.click) {
      this.props.click(name)
    }
  }

  render0() {
    const props = this.props
    const legend = props.legend
    const full = props.full
    const literal = props.literal
    const style = {...props.style}||{}
    const code = props.children
    const desc = literal?codeTypes['literal']:props.type?codeTypes[props.type]:codeDesc(code)

    style.background = desc.color;

    let title
    if(props.title !== undefined) {
      title = props.title
    } else if(legend) {
      title = desc.desc
    } else if(desc.atom) {
      title = atomsDesc[code]+"\n\n"+atoms[code].call+"\n\n"+desc.name+" - "+desc.desc
    } else {
      const quick = quicks[code]
      if(quick) {
        title = quick.title+"\n\n"+desc.name+" - "+desc.desc
      } else if(literal) {
        title = "Literal: number (complex/float/int), string, or list."
      } else {
        title = "?"
      }
    }

    const nbsp = String.fromCharCode(160)

    let label = code===" "?nbsp:code.length === 2 && !full?code.substr(1):code
    if(legend) {
      label = code.length === 2?code.substr(0, 1):nbsp
    }
    return <button 
      className={literal?css.square:css.ball}
      name={code}
      title={legend?desc.desc:title}
      alt={legend?desc.desc:title}
      onClick={this.click.bind(this)}
      style={style}
      >
        {label}
      </button>
  }

  render() {
    const code = this.props.children
    if(!this.comp0 || this.code0 !== code) {
      this.code0 = code
      this.comp0 = this.render0()
    }
    return this.comp0
  }
}
