import atoms from './atoms'
import quicks from './quicks'

const code_page  = 
  "¡¢£¤¥¦©¬®µ½¿€ÆÇÐÑ×ØŒÞßæçðıȷñ÷øœþ !\"#$%&'()*+,-./0123456789:;<=>?@ABCDEFGHIJKLMNOPQRSTUVWXYZ[\\]^_`abcdefghijklmnopqrstuvwxyz{|}~¶" +
  "'''°¹²³⁴⁵⁶⁷⁸⁹⁺⁻⁼⁽⁾ƁƇƊƑƓƘⱮƝƤƬƲȤɓƈɗƒɠɦƙɱɲƥʠɼʂƭʋȥẠḄḌẸḤỊḲḶṂṆỌṚṢṬỤṾẈỴẒȦḂĊḊĖḞĠḢİĿṀṄȮṖṘṠṪẆẊẎŻạḅḍẹḥịḳḷṃṇọṛṣṭ§Äẉỵẓȧḃċḋėḟġḣŀṁṅȯṗṙṡṫẇẋẏż«»‘’“”"

const codeTypeSymbols = (codeType) => {
  const symbols = []

  switch(codeType.type) {
    case "nilad":
    case "Ønilad":
    case "monad":
    case "Æmonad":
    case "Œmonad":
    case "dyad":
    case "ædyad":
    case "œdyad":
      Object.keys(atoms).forEach(c=>{
        if(c.length === codeType.sample.length) {
          const a = atoms[c]
          if(a.arity === codeType.arity && (codeType.prefix.length === 0 || c.startsWith(codeType.prefix))) {
            symbols.push(c)
          }
        }
      })
      break
    case "undef":
      code_page.split("").forEach(c=>{
        const a = atoms[c]
        const q = quicks[c]
        if(!a && !q) {
          symbols.push(c)
        }
      })
      break

    default:
      Object.keys(quicks).forEach(c=>{
        if(c.length === codeType.sample.length) {
          const q = quicks[c]
          if(q.type === codeType.type) {
            symbols.push(c)
          }
        }
      })
      break
  }
  return symbols
}

export default codeTypeSymbols
