import atoms from './atoms'
import quicks from './quicks'
import codeTypes from './codeTypes'

export default (code) => {
  const atom = atoms[code]
  const quick = quicks[code]

  let type

  if(atom) {
    if(code.length >= 2) {
      const c0 = code.charAt(0)
      switch(c0) {
        case "Ø":
          type = "Ønilad";
          break;
        case "Æ":
          type = "Æmonad";
          break;
        case "Œ":
          type = "Œmonad";
          break;
        case "æ":
          type = "ædyad";
          break;
        case "œ":
          type = "œdyad";
          break;
        default:
          type = "undef";
          break;
      }
    } else {
      if(atom.arity === 0) {
        type = "nilad"
      } else if(atom.arity === 1) {
        type = "monad"
      } else if(atom.arity === 2) {
        type = "dyad"
      }
    }
  } else if(quick) {
    type = quick.type
  } else {
    type = "undef"
  }

  return codeTypes[type]
}
