import React, { Component } from 'react'

import css from './InputCode.module.css'

export default class InputCode extends Component {

  constructor() {
    super()

    this.inputRef = React.createRef()
  }

  componentDidMount() {
    this.inputRef.current.focus();
  }

  componentDidUpdate(prevProps) {
    const props = this.props
    if(props.chain !== prevProps.chain) {
      this.inputRef.current.focus();
    }
    this.inputRef.current.setSelectionRange(props.selectionStart, props.selectionEnd);
  }

  change(e) {
    const value = e.target.value
    this.props.change(value)
    this.props.setSelection(e.target.selectionStart, e.target.selectionEnd)
  }

  click(e) {
    this.props.setSelection(e.target.selectionStart, e.target.selectionEnd)
  }

  keyUp(e) {
    this.props.setSelection(e.target.selectionStart, e.target.selectionEnd)
  }

  render() {
    const props = this.props
    const chain = props.chain

    return <input
      className={css.input}
      type="text" 
      name="InputCode" 
      value={chain}
      maxLength={1024}
      onChange={this.change.bind(this)}
      onClick={this.click.bind(this)}
      onKeyUp={this.keyUp.bind(this)}
      ref={this.inputRef}
    />
  }

}
