import atoms from './atoms'
import quicks from './quicks'

const chain_separators = {
	'ø': [0, '', true],
	'µ': [1, '', true],
	')': [1, '€', true],
	'ð': [2, '', true],
	'ɓ': [2, '', false]
}

const str_arities = Object.keys(chain_separators).join('')
const str_strings = '“[^«»‘’”]*[«»‘’”]?'
const str_charlit = '”.'
const str_chrpair = '⁾..'
const str_intpair = '⁽..'
const str_realdec = '(0|-?\\d*\\.\\d*|-?\\d+|-)'
const str_realnum = ['(', '?ȷ', '?|', ')'].join(str_realdec)
const str_complex = ['(', '?ı', '?|', ')'].join(str_realnum)
const str_literal = '('+[str_strings, str_charlit, str_chrpair, str_complex, str_intpair].join('|')+')'
const str_litlist = '\\[*' + str_literal + '((\\]*,\\[*)' + str_literal + ')*\\]*'

const  escapeRegExp = (string) => {
  return string.replace(/[.*+?^${}()|[\]\\]/g, '\\$&'); // $& means the whole matched string
}

const as = Object.keys(atoms)
const qs = Object.keys(quicks).filter(k=>quicks[k].type === "quick" || quicks[k].type === "Ðquick")
const str_nonlits = as.concat(qs).map(escapeRegExp).join('|')

export const regex_chain = new RegExp('(^(' + str_nonlits + '|' + str_litlist + '| )+|[' + str_arities + '])(' + str_nonlits + '|' + str_litlist + '| )*', 'g')
export const regex_liter = new RegExp(str_litlist, 'g')
export const regex_token = new RegExp(str_nonlits + '|' + str_litlist, 'g')
export const regex_flink = new RegExp('([' + str_arities + ']|' + str_nonlits + '|' + str_litlist + ')', 'g')

const findAll = (rx, s) => {
  var matches = [];
  var match
  while((match = rx.exec(s)) !== null) {
     matches.push(match[0]);
  }
  return matches
}

export const parseChain = (chain) => {
  return findAll(regex_flink, chain)
}

export const parseLiter = (litlist) => {
  return findAll(regex_liter, litlist)
}
